/* ######################################ProgressBar-CPP############################################ */
.progress-bar-cpp {
    width: 260px;
    height: 20px;
    background-color: #f1f1f1;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    margin-bottom: 10px;
  }
  
  .progress-bar-cpp::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    background-color: #FC7300;
    transition: width 0.5s ease-in-out;
    animation: progress-animation-cpp 3s linear forwards;
  }
  
  .progress-bar-cpp::after {
    content: attr(data-label);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-weight: bold;
  }
  
  @keyframes progress-animation-cpp {
    0% { width: 0; }
    100% { width: 85%; }
  }

  .progress-bar-cpp:hover::before {
    width: 100%;
  }
  
  .progress-bar-cpp:hover::after {
    text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.8);
  }
