.gray-scale-img {
  filter: grayscale(60%); /* Apply grayscale filter to make the image gray */
  transition: filter 0.7s ease; /* Add a transition effect for smooth hover animation */
}

.gray-scale-img:hover {
  filter: grayscale(0%); /* Remove grayscale filter on hover to show color */
}

.image {
  width: 70px; /* Set the width of the image */
  height: auto; /* Automatically adjust the height to maintain the aspect ratio */
}
.image-jj{
  width: 87px; 
  height: auto;
}

.container-img {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}
.container-progress {
  display: flex;
  justify-content: center;
  align-items: center;
}



.virt{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 10px;
}
.hor{
    display: flex;
    flex-direction: column;
}
.grid-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5px;
  }
  
  .grid-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* padding: 10px; */
  }
@media (min-width: 850px){
    .progress-bar {
      width: 260px;
    }
    .grid-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0px;
      }
    .container-img{
      margin-right: 30px;
    }
    .virt{
      flex-direction: row;

    }
}