.contact-me-container {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 10px;
  }
  
  h1 {
    text-align: center;
    color: #925000;
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  label {
    color: #ff8c00;
    margin-bottom: 5px;
  }
  
  input,
  textarea {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  input[type='submit'] {
    background-color: #b47c36;
    color: #fff;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  input[type='submit']:hover {
    background-color: #f17501;
  }
  
  input[type='submit']:focus {
    outline: none;
  }
  
  textarea {
    height: 100px;
  }

  .success-message {
    background-color: #4ec952;
    color: #fff;
    padding: 10px;
    text-align: center;
    margin-top: 10px;
    border-radius: 3px;
    max-width: 200px;
    align-items: center;
}



  .grid-container-contact{
    display: grid;
    grid-template-columns: 2fr 1fr;
 
  }

  .button-shade{
    border-color: #ddd;
    box-shadow: 2px 2px 2px rgb(122, 135, 139);
  }