
  
  .progress-overlay {

    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #817a7a7c;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    padding: 10px;
    box-shadow: 5px 5px 10px rgb(152, 163, 149);
    /* opacity: 0.5; */
  }
  .progress1{
    width: 90.43%;
    height: 90.43%;
    border-radius: 50%;
    background-color: #f17501;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: pulsate 5s infinite;
  }
  .progress2{
    width: 91.8%;
    height: 91.8%;
    border-radius: 50%;
    background-color: #f17501;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: pulsate 6s infinite;
  }
  .progress3{
    width: 88%;
    height: 88%;
    border-radius: 50%;
    background-color: #f17501;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: pulsate 7s infinite;
  }


  @keyframes pulsate {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(0.5);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .acc-achv{
    background-color: #C4D7B2;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 5px 5px 10px rgb(152, 163, 149);
  }

  .image_achv{
    height: auto;
    width: auto;
    /* padding: 0%; */
    /* margin: 10%; */
  }

  @media (min-width: 820px) {
    .image_achv:hover {
        position: absolute;
        width: 40%;
        height: auto;
        transform: scale(1.3);
        transition: transform 2s ease;
        z-index: 1;
    }
    .image_achv:not(:hover) {
        transform: scale(1);
        transition: transform 2s ease;
      }
  }
  .lnk:link,
  .lnk:visited {
    color: inherit; 
    text-decoration: none;
  }
  
  .lnk:active {
    color: inherit; 
  }
  